import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnDestroy,
  OnInit,
  Output,
  QueryList,
  ViewChild,
  ViewChildren
} from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { OverviewListConfig } from '@sofico-framework/ui-kit/classes';
import { OverviewListSearchBarComponent } from '@sofico-framework/ui-kit/components/overview-list-search-bar';
import {
  OnSofFocus,
  SOF_FOCUS_COMPONENT
} from '@sofico-framework/ui-kit/directives/focus';
import {
  GroupDefinition,
  GroupDefinitionWithIdentifiers
} from '@sofico-framework/ui-kit/types';
import { search, SortingOrderConfig, sortList } from '@sofico-framework/utils';
import { takeUntilDestroy, UntilDestroy } from 'ngx-reactivetoolkit';
import {
  BehaviorSubject,
  combineLatest,
  Observable,
  of,
  ReplaySubject
} from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  map,
  shareReplay,
  skip,
  switchMap
} from 'rxjs/operators';
import { OverviewListItemComponent } from '../overview-list-item/overview-list-item.component';

@UntilDestroy()
@Component({
  selector: 'sof-overview-list',
  changeDetection: ChangeDetectionStrategy.OnPush,
  template: `
    <ng-container *ngIf="config$ | async as config">
      <div *ngIf="enableSearch || enableSorting" class="row">
        <div
          *ngIf="enableSearch"
          [ngClass]="enableSorting ? 'col-lg-8 col-md-6 col-sm-12' : 'col-12'"
        >
          <sof-overview-list-search-bar
            #searchBarComponent
            [tc]="tc"
            (changeTerm)="onChangeTerm($event)"
          ></sof-overview-list-search-bar>
        </div>
        <div
          *ngIf="enableSorting"
          [ngClass]="
            enableSearch
              ? 'col-lg-4 col-md-6 col-sm-12'
              : 'col-lg-4 offset-lg-8  col-md-12 col-sm-12'
          "
        >
          <sof-overview-list-sort-dropdown
            [tc]="tc"
            [sorting]="sortingSub$ | async"
            [overviewListConfig]="config"
            (changeSorting)="onChangeSorting($event)"
          ></sof-overview-list-sort-dropdown>
        </div>
      </div>
      <div *ngIf="groupDefinitions" class="row">
        <sof-overview-list-group-tabs
          [tc]="tc"
          class="col-lg-12 col-md-12 col-sm-12 mb-3"
          [entities]="searchedEntities$ | async"
          [selectedGroup]="selectedGroupSub$ | async"
          [groupDefinitions]="groupDefinitions"
          [groupSelector]="groupSelector"
          (selectGroup)="onChangeGroup($event)"
        ></sof-overview-list-group-tabs>
      </div>
      <ng-container *ngIf="hasFilteredEntities$ | async; else noResults">
        <sof-in-view [preloadHeight]="500" (inView)="onInView($event)">
          <sof-overview-list-item
            #listComponent
            *ngFor="let entity of filteredEntities$ | async; trackBy: trackByFn"
            [entity]="entity"
            [tc]="tc"
            [dynamicRowComponent]="config?.dynamicRowComponent"
          >
          </sof-overview-list-item>
        </sof-in-view>
      </ng-container>
      <ng-template #noResults>
        <ng-container *ngIf="entities$ | async as entities">
          <ng-container *ngIf="entities?.length > 0; else noData">
            {{ tc + '.' + 'SEARCH-NO-RESULTS' | translate }}
          </ng-container>
          <ng-template #noData>
            {{ tc + '.' + 'SEARCH-NO-DATA' | translate }}
          </ng-template>
        </ng-container>
      </ng-template>
    </ng-container>
  `,
  providers: [
    { provide: SOF_FOCUS_COMPONENT, useExisting: OverviewListComponent }
  ]
})
export class OverviewListComponent<T> implements OnInit, OnDestroy, OnSofFocus {
  /**
   * The translation context.
   */
  @Input() tc: string;

  /**
   * The overview list config.
   */
  @Input() set config(config: OverviewListConfig<T>) {
    if (config) {
      this.config$.next(config);
      this.sortingSub$.next(config.initialSorting);
      this.searchableSelectors = config.functionalProps
        .filter(prop => prop.searchable)
        .map(prop => prop.selector);
    }
  }

  /**
   * The entities we want to render in this list.
   */
  @Input() set entities(entities: T[]) {
    if (entities) {
      this.entities$.next(entities);
    }
  }

  /**
   * We can pass group definitions to divide the entities into different groups.
   */
  @Input() groupDefinitions: GroupDefinition[];

  /**
   * The amount of items shown in the list.
   */
  @Input() thresholdNumberOfItems = 15;

  /**
   * The selector to determine which entity belongs in which group.
   */
  @Input() groupSelector: (T) => string | number;

  /**
   * Whether the search bar is visible.
   */
  @Input() enableSearch = true;

  /**
   * Whether the sorting dropdown is visible.
   */
  @Input() enableSorting = true;

  /**
   * Whether we define the last tab state through queryParam.
   */
  @Input() retainGroupSelection = true;

  @Output() searchedEntities = new EventEmitter<T[]>();

  @ViewChild('searchBarComponent')
  searchBarComponent: OverviewListSearchBarComponent<T>;
  @ViewChildren('listComponent') listComponents: QueryList<
    OverviewListItemComponent<T>
  >;

  searchableSelectors: ((entity: T) => any)[];

  // source streams
  entities$ = new ReplaySubject<T[]>(1);
  config$ = new ReplaySubject<OverviewListConfig<T>>(1);
  sortingSub$ = new ReplaySubject<SortingOrderConfig<T>>(1);
  termSub$ = new BehaviorSubject<string>('');
  private numberOfItemsToDisplaySub$ = new BehaviorSubject<number>(
    this.thresholdNumberOfItems
  );

  // intermediate streams
  searchedEntities$: Observable<T[]>;
  selectedGroupSub$ = new BehaviorSubject<GroupDefinition>(null);
  private groupedEntities$: Observable<T[]>;
  private sortedEntities$: Observable<T[]>;

  // presentations streams
  filteredEntities$: Observable<T[]>;
  hasFilteredEntities$: Observable<boolean>;

  trackByFn = i => i;

  constructor(private router: Router, private activatedRoute: ActivatedRoute) {}

  sofFocus(): void {
    if (this.enableSearch) {
      this.searchBarComponent.sofFocus();
    } else {
      this.listComponents.first?.sofFocus();
    }
  }

  ngOnInit(): void {
    // intermediate streams
    this.searchedEntities$ = this.getSearchedEntities$();
    this.groupedEntities$ = this.getGroupedEntities$();
    this.sortedEntities$ = this.getSortedEntities$();

    this.searchedEntities$
      .pipe(takeUntilDestroy(this))
      .subscribe(searchedEntities =>
        this.searchedEntities.emit(searchedEntities)
      );

    // presentation streams
    this.filteredEntities$ = this.sortedEntities$;
    this.hasFilteredEntities$ = this.getHasFilteredEntities$();

    if (this.groupDefinitions?.length > 0) {
      const tab = this.activatedRoute.snapshot.queryParamMap.get('tab');
      if (tab) {
        this.selectedGroupSub$.next(
          this.groupDefinitions.find(x => x.id === tab)
        );
      } else {
        this.selectedGroupSub$.next(this.groupDefinitions[0]);
      }
    } else {
      this.selectedGroupSub$.next(null);
    }

    this.setupQueryParamSubscription();
  }

  ngOnDestroy(): void {}

  onChangeSorting(sorting: any): void {
    this.sortingSub$.next(sorting);
    this.numberOfItemsToDisplaySub$.next(this.thresholdNumberOfItems);
  }

  onChangeTerm(term: string): void {
    this.termSub$.next(term);
    this.numberOfItemsToDisplaySub$.next(this.thresholdNumberOfItems);
  }

  onChangeGroup(group: any): void {
    this.selectedGroupSub$.next(group);
    this.numberOfItemsToDisplaySub$.next(this.thresholdNumberOfItems);
  }

  onInView(value: boolean): void {
    if (value) {
      this.numberOfItemsToDisplaySub$.next(
        this.numberOfItemsToDisplaySub$.getValue() + this.thresholdNumberOfItems
      );
    }
  }

  private getSearchedEntities$(): Observable<T[]> {
    return this.entities$.pipe(
      // we need switchMap and of because the search takes a term observable instead of a term
      switchMap(results =>
        this.enableSearch && this.searchableSelectors.length !== 0
          ? of(results).pipe(search(this.termSub$, this.searchableSelectors))
          : of(results)
      ),
      shareReplay({ refCount: true, bufferSize: 1 })
    );
  }

  private getGroupedEntities$(): Observable<T[]> {
    return combineLatest([this.searchedEntities$, this.selectedGroupSub$]).pipe(
      map(([items, selectedGroup]) => {
        if (!selectedGroup) {
          return items;
        }

        // todo: Remove 'groupIdentifiers' in next major release.
        return selectedGroup.hasOwnProperty('groupIdentifiers') ||
          selectedGroup.hasOwnProperty('groupIdentifiersIncl') ||
          selectedGroup.hasOwnProperty('groupIdentifiersExcl')
          ? items?.filter(item => {
              const selector = this.groupSelector(item);
              const groupDefinition = selectedGroup as GroupDefinitionWithIdentifiers;
              let groupIdentifiersIncluded: (string | number)[];
              let groupIdentifiersExcluded: (string | number)[];

              // todo: Remove 'groupIdentifiers' in next major release.
              if (selectedGroup.hasOwnProperty('groupIdentifiers')) {
                groupIdentifiersIncluded = groupDefinition.groupIdentifiers;
              } else {
                groupIdentifiersIncluded = groupDefinition.groupIdentifiersIncl;
                groupIdentifiersExcluded = groupDefinition.groupIdentifiersExcl;
              }

              if (Array.isArray(selector)) {
                const {
                  groupIdentifiersInclStrategy,
                  groupIdentifiersExclStrategy
                } = groupDefinition;
                const includesFn = (v: string | number) => selector.includes(v);

                // If either incl. or excl. is undefined we want to ignore said filter and return true (see: ?? true).
                // It's only needed for the incl. filters as for excl. !undefined = true.
                return (
                  ((groupIdentifiersInclStrategy === undefined ||
                  groupIdentifiersInclStrategy === 'or'
                    ? groupIdentifiersIncluded?.some(includesFn)
                    : groupIdentifiersIncluded?.every(includesFn)) ??
                    true) &&
                  (groupIdentifiersExclStrategy === undefined ||
                  groupIdentifiersExclStrategy === 'or'
                    ? !groupIdentifiersExcluded?.some(includesFn)
                    : !groupIdentifiersExcluded?.every(includesFn))
                );
              }

              return (
                groupIdentifiersIncluded?.includes(selector) &&
                !groupIdentifiersExcluded?.includes(selector)
              );
            })
          : items;
      })
    );
  }

  private getSortedEntities$(): Observable<T[]> {
    return combineLatest([
      this.groupedEntities$,
      this.sortingSub$,
      this.numberOfItemsToDisplaySub$
    ]).pipe(
      map(([entities, sorting, numberOfItemsToDisplay]) =>
        sortList(entities, sorting).slice(0, numberOfItemsToDisplay)
      )
    );
  }

  private getHasFilteredEntities$(): Observable<boolean> {
    return this.filteredEntities$.pipe(
      map(entities => entities?.length > 0),
      shareReplay({ refCount: true, bufferSize: 1 })
    );
  }

  private setupQueryParamSubscription(): void {
    this.selectedGroupSub$
      .pipe(
        skip(1),
        map(selectedGroup => selectedGroup?.id),
        filter(selectedGroupId => !!selectedGroupId),
        distinctUntilChanged(),
        takeUntilDestroy(this)
      )
      .subscribe(tab => {
        if (this.retainGroupSelection) {
          this.router.navigate([], {
            relativeTo: this.activatedRoute,
            queryParams: { tab },
            queryParamsHandling: 'merge',
            replaceUrl: true
          });
        }
      });
  }
}
